html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
  display: flex;
  justify-content: center;
  background-color: rgb(235, 235, 235);
}

.container {
  width: 100%;
  max-width: 550px;
  margin-top: 5%;
  padding-bottom: 25px;
  border-radius: 15px;
  border: 16px solid black;
  background-color: #c00025;
}

.logo-container {
  padding-top: 5%;
  padding-left: 5%;
}

.display {
  height: 100px;
  width: 450px;
  border-radius: 50px;
  color: white;
  background-color: #322c2d;
  box-shadow: 1px 1px #322c2d;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  padding-right: 8%;
  font-size: 8rem;
  margin: 3% auto 6% auto;
}

.button-container {
  display: flex;

}

.number-container {
  width: 70%;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  align-content: center;
  margin-left: 2%;
}

.number-container button {
  font-size: 3rem;
  width: 100px;
  height: 100px;
  color: white;
  background-color: #184379;
  border: none;
  border-bottom: 4px solid black;
  border-radius: 50px;
  margin: 2%;
}

button.special-button {
  background-color: #225d9e;
}

.number-button:nth-child(13) {
  width: 215px;
  border-radius: 35px;
}

.operator-container {
  width: 30%;
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
  align-content: flex-start;
}

.operator-container button {
  font-size: 3rem;
  width: 100px;
  height: 100px;
  color: white;
  background-color: #237293;
  border: none;
  border-bottom: 4px solid black;
  border-radius: 50px;
  margin: 4.5%;
}

button:active {
  border-bottom: 4px solid #c00025;
}